import * as types from "../constants/ActionTypes";

const emptyTask = {
	id: undefined,
	index: undefined,
	title: "",
	content: "",
	termDate: "",
	isClosed: false
};

const initialState = {
	activeTask: {},
	list: [emptyTask]
};

export default function tasks(state = initialState, action) {
	switch (action.type) {
		case types.SELECT_TASK:
			return {
				...state,
				activeTask: action.payload
			};

		case types.SAVE_TASK:
			return {
				...state,
				list: action.payload
			}

		case types.SORT_TASKS:
			return {
				...state,
				list: action.payload
			};

		case types.CREATE_TASK:
			return {
				...state,
				list: action.payload
			}

		case types.DELETE_TASK:
			return {
				...state,
				list: action.payload,
				activeTask: emptyTask
			}

		case types.CHANGE_TASK_INDEXES:
			return {
				...state,
				list: action.payload
			};

		case types.FETCH_TASKS:
			return {
				...state
			};

		case types.FETCH_TASKS_SUCCESS:
			return {
				...state,
				list: action.payload
			};

		default:
			return state;
	}
}


