import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import TaskForm from "./taskform";
import { initialize } from "redux-form";
import { saveTask, closeTask, createTask, deleteTask } from "../actions/index";

class TaskDetails extends Component {
	constructor(props) {
		super(props);
		let { task } = this.props;
		initialize(task);
	}

	handleSubmit = task => {
		this.props.saveTask(task);
	};

	handleCloseTask = () => {
		let task = { ...this.props.task };
		task.isClosed = true;
		this.props.saveTask(task);
	};

	handleDeleteTask = () => {
		this.props.deleteTask(this.props.task.id)
	};

	handleCreateTask = () => {
		this.props.createTask();
	};

	render() {
		if (!this.props.task) return <div></div>

		return (
			<div className="panel panel-default">
				<div className="panel-heading">
					<strong>Задача</strong>
				</div>
				<div className="panel-body">
					<TaskForm
						onSubmit={this.handleSubmit}
						handleCloseTask={this.handleCloseTask}
						handleCreateTask={this.handleCreateTask}
						handleDeleteTask={this.handleDeleteTask}
						isSelectedTask={typeof(this.props.task.id) === 'undefined' ? true : false}
					/>
				</div>
			</div>
		);
	}
}
function mapStateToProps(state) {
	return {
		task: state.tasks.activeTask
	};
}

function mapDispatchToProps(dispatch) {
	return bindActionCreators(
		{
			saveTask: saveTask, 
			closeTask: closeTask, 
			createTask: createTask, 
			deleteTask: deleteTask
		},
		dispatch
	);
}

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(TaskDetails);



