import React, { Component } from 'react';

class TaskItem extends Component {
    render() {
        let item = { ...this.props.item };
        let now = new Date();
        let limitDate = new Date();
        limitDate.setDate(now.getDate() + 3);
        const adate = item.termDate.split('-');
        let taskDate = new Date(adate[0], adate[1] - 1, adate[2]);

        return (
            <li className={'list-group-item noselect ' + ((taskDate < now && !item.isClosed) ? 'list-group-item-danger' : '') + ((taskDate > now && taskDate < limitDate && !item.isClosed) ? 'list-group-item-warning' : '') + (item.isClosed ? 'list-group-item-success' : '')}>
                {item.title}<br />
                <small>Срок: {item.termDate}</small>
            </li>
        );
    }
}


export default TaskItem;


