import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { connect } from 'react-redux';

const required = value => value ? '' : 'Required';

class TaskForm extends React.Component {
    handleInitialize() {
        this.props.initialize({ ...this.props.task });
    }

    componentWillMount = () => {
        this.handleInitialize();
    }

    reset = () => {
        this.props.initialize({
            id: 0,
            index: 0,
            title: "",
            content: "",
            termDate: "",
            isClosed: false
        });
    }

    render() {
        const { handleSubmit, handleCloseTask, handleCreateTask, handleDeleteTask, isSelectedTask } = this.props;

        return (
            <div>
                <form onSubmit={handleSubmit}>
                    <Field type='input' name='title' component={renderTextField} label='Название' validate={required} />
                    <Field type='input' name='content' component={renderTextAreaField} label='Описание' />
                    <Field type="date" name='termDate' component={renderDateField} label='Срок' validate={required} />
                    <div className='btn-group'>
                        <button className='btn btn-default' type='button' onClick={handleCreateTask}>Создать</button>
                        <button className='btn btn-default' type='submit' disabled={isSelectedTask}>Сохранить</button>
                        <button className='btn btn-default' type='button' onClick={handleCloseTask} disabled={isSelectedTask}>Закрыть</button>
                        <button className='btn btn-default' type='button' onClick={handleDeleteTask} disabled={isSelectedTask}>Удалить</button>
                    </div>
                </form>
            </div >
        );
    }
}

function mapStateToProps(state) {
    return {
        initialValues: state.tasks.activeTask
    };
}

TaskForm = reduxForm({
    form: 'task',
    fields: ['title', 'content', 'termDate'],
    enableReinitialize: true,
})(TaskForm);

export default connect(mapStateToProps)(TaskForm);

const renderTextField = ({ input, label, type, meta: { touched, error } }) => (
    <div>
        <label>{label}</label>
        <div className='form-group'>
            <input {...input} placeholder={label} type='text' className="form-control" />
            {touched && (error && <span className="text-danger">{error}</span>)}
        </div>
    </div>
);

const renderTextAreaField = ({ input, label, type, meta: { touched, error } }) => (
    <div>
        <label>{label}</label>
        <div className='form-group'>
            <textarea {...input} placeholder={label} className="form-control"></textarea>
            {touched && (error && <span className="text-danger">{error}</span>)}
        </div>
    </div>
);

const renderDateField = ({ input, label, type, meta: { touched, error } }) => (
    <div>
        <label>{label}</label>
        <div className='form-group'>
            <input {...input} placeholder={label} type='date' className="form-control" />
            {touched && (error && <span className="text-danger">{error}</span>)}
        </div>
    </div>
);



