import * as types from "../constants/ActionTypes";
import axios from "axios";
import {
	apiURL
} from "../config/index";

export const select = task => {
	return {
		type: types.SELECT_TASK,
		payload: task
	};
};
export const sort = tasks => {
	return {
		type: types.SORT_TASKS,
		payload: tasks
	};
};

export function fetchTasks() {
	return function (dispatch) {
		axios
			.get(`${apiURL}/task`)
			.then(response => {
				dispatch({
					type: types.FETCH_TASKS_SUCCESS,
					payload: response.data
				});
			})
			.catch(error => {
				console.log(error);
			});
	};
}

export function saveTask(task) {
	return function (dispatch) {
		axios
			.post(`${apiURL}/task`, task)
			.then(response => {
				dispatch({
					type: types.SAVE_TASK,
					payload: response.data
				});
			})
			.catch(error => {
				console.log(error);
			});
	};
}

export function closeTask(task) {
	return function (dispatch) {
		axios
			.post(`${apiURL}/task/close`, task)
			.then(response => {
				dispatch({
					type: types.CLOSE_TASK,
					payload: response.data
				});
			})
			.catch(error => {
				console.log(error);
			});
	};
}

export function changeTaskIndexes(indexes) {
	return function (dispatch) {
		axios
			.post(`${apiURL}/task/newindexes`, indexes)
			.then(response => {
				dispatch({
					type: types.CHANGE_TASK_INDEXES,
					payload: response.data
				});
			})
			.catch(error => {
				console.log(error);
			});
	};
}

export function createTask() {
	return function (dispatch) {
		axios
			.post(`${apiURL}/task`, {})
			.then(response => {
				dispatch({
					type: types.CREATE_TASK,
					payload: response.data
				});
			})
			.catch(error => {
				console.log(error);
			});
	};
}

export function deleteTask(taskId) {
	return function (dispatch) {
		axios
			.delete(`${apiURL}/task/${taskId}`)
			.then(response => {
				dispatch({
					type: types.DELETE_TASK,
					payload: response.data
				});
			})
			.catch(error => {
				console.log(error);
			});
	};
}


