import React, { Component } from "react";
import "../styles/tasklist.scss";
import {
	SortableContainer,
	SortableElement,
	arrayMove
} from "react-sortable-hoc";
import TaskItem from "../containers/taskitem";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { select, sort, fetchTasks, changeTaskIndexes } from "../actions/index";

const SortableItem = SortableElement(({ item }) => <TaskItem item={item} />);

const SortableList = SortableContainer(({ items }) => {
	
	if(items == "") return <div></div>;

	return (
		<ul className="list-group">
			{items.map((item, index) => (
				<SortableItem key={`item-${index}`} index={index} item={item} />
			))}
		</ul>
	);
});

class TaskList extends Component {
	componentDidMount = () => {
		this.props.fetchTasks();
	};

	onSortEnd = ({ oldIndex, newIndex }) => {

		if (oldIndex === newIndex) {
			this.props.select(this.props.tasks.list[oldIndex]);
		} else {
			const newOrderedList = arrayMove(this.props.tasks.list, oldIndex, newIndex);
			this.props.sort(newOrderedList);

			let newIndexes = [];

			newOrderedList.map((item, index) => {
				newIndexes.push({ id: item.id, value: index });
			});
			this.props.changeTaskIndexes(newIndexes);
		}
	};

	render() {
		return (
			<SortableList items={this.props.tasks.list} onSortEnd={this.onSortEnd} />
		);
	}
}

function mapStateToProps(state) {
	return {
		tasks: state.tasks
	};
}

function matchDispatchToProps(dispatch) {
	return bindActionCreators({ select: select, sort: sort, fetchTasks: fetchTasks, changeTaskIndexes: changeTaskIndexes }, dispatch);
}

export default connect(
	mapStateToProps,
	matchDispatchToProps
)(TaskList);



